import React, { useState } from 'react'
import {Link} from "react-router-dom";
import MoviesConsumer from "../context"

const SEARCH_API = "https://api.themoviedb.org/3/search/movie?&"+
                   "api_key=04c35731a5ee918f014970082a0088b1&query=";

 function Navbar() {

    var state = {
      navbarMovies : []
    }

    const [searchTerm , setSearchTerm] = useState('');

     const handleOnSubmit = (dispatch,e) =>{
       e.preventDefault();
        fetch(SEARCH_API + searchTerm)
        .then(resp => resp.json())
        .then(data => {
          state  = {
            ...state,
            navbarMovies : data.results
          }
          dispatch({
            type : "UPDATE_MOVIE_LIST",
            payload : state.navbarMovies
          })
        });
      }
    
      const handleOnChange = (e) =>{
        setSearchTerm(e.target.value);
      }
      

    return (
        <MoviesConsumer>
          {
            value =>{
              const {dispatch,resetMainPage} = value;
              return(
              <header>
                <ul>
                <Link to="/" onClick={resetMainPage}>
                        <li className="logo" >Home</li>
                </Link>
                        <li>
                          <form onSubmit={handleOnSubmit.bind(this,dispatch)}>
                            <input 
                            className="search"
                            type="search" 
                            placeholder="Search Movie"
                            target={searchTerm}
                            onChange={handleOnChange}> 
                            </input>
                          </form>
                        </li>
                </ul>
            </header>
            )
            }}
        </MoviesConsumer>
    )
}

export default Navbar;