import React, { Component } from 'react'

const MoviesContext = React.createContext();

const reducer = (state, action) => {
    switch (action.type) {
        case "UPDATE_MOVIE_LIST":
            return {
                ...state,
                movies: action.payload
            }
        case "UPDATE_PAGE":
            return {
                ...state,
                page: action.payload
            }
        default:
            break;
    }
}

const FEATURED_API = "https://api.themoviedb.org/3/discover/movie/?" +
    "api_key=04c35731a5ee918f014970082a0088b1&page=";

export class MoviesProvider extends Component {

    state = {
        movies: [],
        dispatch: action => {
            this.setState(state => reducer(state, action));
        },
        resetMainPage: () => {
            fetch(FEATURED_API + 1)
                .then(resp => resp.json())
                .then(data => {
                    this.setState({
                        movies: data.results,
                        page: 1
                    })
                });
        },
        page: 1
    }

    componentDidMount() {
        const { page } = this.state;
        fetch(FEATURED_API + page)
            .then(resp => resp.json())
            .then(data => {
                this.setState({
                    movies: data.results
                })
            });
    }

    render() {
        return ( 
            <div>
            <MoviesContext.Provider value={this.state}> 
                {this.props.children} 
            </MoviesContext.Provider> 
            </div>
        )
    }
}


const MoviesConsumer = MoviesContext.Consumer;

export default MoviesConsumer;