import React from 'react';
import MoviesConsumer from "../context";

const FEATURED_API = "https://api.themoviedb.org/3/discover/movie/?"+
"api_key=04c35731a5ee918f014970082a0088b1&page=";


const transition  = () => {

    const backPage = (page,dispatch,e)  => {
        page--;
        change(page,dispatch);
    }

    const nextPage = (page,dispatch,e)  => {
        page++;
        change(page,dispatch);
    }
    
    const lastPage = (page,dispatch,e)  => {
        page = 74;
        change(page,dispatch);
    }

     
    const firstPage = (page,dispatch,e)  => {
        page = 1;
        change(page,dispatch);
    }

    function change(page,dispatch){
        window.scrollTo(0,0)
        fetch(FEATURED_API + page)
        .then(resp => resp.json())
        .then(data => {
            dispatch({
                type : "UPDATE_MOVIE_LIST",
                payload : data.results
            });
            dispatch({
                type : "UPDATE_PAGE",
                payload : page
            })
        });
    }

    return (
        <MoviesConsumer>
            {
                value =>{ 
                    const {page,dispatch}  = value;
                    return(
                        <div className="transition">
                                <div onClick={firstPage.bind(this,page,dispatch)} className="goFirst">
                                    <i className="fas fa-backward"></i>
                                </div>
                                <div onClick={backPage.bind(this,page,dispatch)} style={ page===1 ? {display:"none"} : {display:"block"}}>{page-1}</div>
                                <div id="active">{page}</div>
                                <div onClick={nextPage.bind(this,page,dispatch)} style={ page===74 ? {display:"none"} : {display:"block"}}>{page+1}</div>
                                <div onClick={lastPage.bind(this,page,dispatch)} className="goEnd">
                                    <i className="fas fa-forward"></i>
                                </div>
                        </div>   
                    )
                }
            }
        </MoviesConsumer>
    )
}

export default transition;