import React, { Component} from 'react'
import MoviesConsumer from "../context"
import Notfound from "../pages/NotFound"
import Goback from "../pages/goBack"

const IMAGE_API= "https://image.tmdb.org/t/p/w1280/";

class MovieInfo extends Component {
    render() {
        return (
            <MoviesConsumer>
                {
                    value =>
                    {
                        const id = this.props.match.params.id;
                        const movies = value.movies.filter(movie =>{
                            return movie.id === parseInt(id);
                        }
                        );
                        const {title,poster_path,release_date,vote_average,overview} =
                        movies.length === 0 ? <Notfound/> : movies[0];
                        
                        return(
                            <>
                                <Goback/>
                                <div className="movieInfoContainer">
                                    <div className="img">
                                        <img src={IMAGE_API + poster_path} alt={`${title} is poster`}></img>
                                    </div>
                                    <div className="info">
                                        <h2>{title}</h2>
                                        <div className="icon">
                                            <i className="fas fa-calendar-day"/>
                                            <p className="date">{release_date}</p>
                                        </div>
                                        <div className="icon">
                                            <i className="fas fa-star"/>
                                            <p>{vote_average}</p>
                                        </div>
                                        <p className="infoText">{overview}</p>
                                    </div>
                                </div>
                            </>
             )
            }
            }
            </MoviesConsumer>
        )
    }
}

export default  MovieInfo;