import React from 'react'
import {Link} from "react-router-dom";
import "../index.css"

function Movie(props) {
   
    const IMAGE_API= "https://image.tmdb.org/t/p/w1280/";

    const {id,title,poster_path} = props;

    return (
        <div className="movie-cover">
            <Link to={`info-${id}`}>
                <div className="movie">
                    <img src={
                        poster_path ? IMAGE_API + poster_path 
                        : "https://cdn03.ciceksepeti.com/cicek/hf2845-1/L/nostaljik-film-makinasi-hf2845-1-1.jpg"
                    } 
                    alt={title}>
                    </img>
                    <span className="details">View Details</span>
                </div>
            </Link>

        </div>
    )
}


export default  Movie;