import React from 'react'
import Goback from "../pages/goBack"

 function NotFound() {


    return (
        <div className="notfound">
            <Goback/>
            <h1>
                <p>404 NOT FOUND</p>
                <p>This web adress is not used. Please enter the valid web adress.</p>
            </h1>
            <hr></hr>
        </div>
    )
}

export default NotFound;